.notOpened-menu{
  position: fixed;
  height: 100vh;
  width: 4vw;
  background-color: rgb(230, 152, 152);
}
.menu{
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: aquamarine;
  width: 18vw;
  z-index: 3;
  transition: margin 1s;
  margin: 0 0 0 -18vw;
}
.menu.menuOpen{
  margin: 0;
}
.menu-option{
  font-size: 25px;
  cursor: pointer;
  margin-left: 20px;
  margin-top: 10px;
  padding-right: 25%;
  display: flex;
  width: fit-content;
}
.homepage{
  position: relative;
  left: 4vw;
  width: 96vw;
  background-color: rgb(158, 203, 240);
  height: 100vh;
  display: grid;
  grid-template-columns: 48vw 48vw;
  grid-template-rows: 50vh 50vh;
}
#menu-close{
  position: absolute;
  top: 4px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
}
.short-info-about-me{
  font-size: 18px;
  margin-left: 8%;
}
.my-name{
  margin-right: 65%;
  font-size: 25px;
}
.my-intro{
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.intro-photo{
  height: 100%;
  width: 100%;
}
.intro-photo-container{
 border: 15px solid rgb(158, 203, 240);
}
.about-me{
  padding: 2vh 2vw;
  width: 96vw;
  height: 100%;
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  font: inherit;
  align-items: center;
}
.about-me-text{
  /* position: relative; */
  /* left: 7%; */
  display: flex;
  flex-direction: column;
  font: inherit;
  align-items: center;
}
.about-me-img{
  padding: 2% 10%;
  width: 30%;
  height: 90%;
}
.submit-button{
  background-color: rgb(242, 242, 246);
  color: rgb(4, 4, 4);
  border-radius: 30px;
  font-size: 1.4em;
  cursor: pointer;
}
img { transition: filter 1s linear; }
.blur { filter: blur(30px); }

.color-change {animation: change-color 1s linear infinite;}

@keyframes change-color{
  0% {background-color: white;}
  100%{background-color: black;}
}
.link-image{
  height: 60px;
  width: 60px;
}
.my-projects{
  padding: 4vh 4vw;
  width: 92vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  margin-bottom: 200px;
}

body{
  background-color: black;
  overflow-x: hidden;
  font-size: calc(1em + 1vw);
  color: whitesmoke;
  height: 100%;
  margin: 0;
  padding: 0;
}
html {
  margin: 0px;
  height: 100%;
  width: 100%;
}



.front-page{
  width: 99vw;
  height: 99vh;
  display: flex;
  align-items: center;
}
#front-page-image{
  width: 60vw;
}
.front-page-text{
  position: relative;
  left: 10vw;
  width: 40vw;
  height: 99vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 3;
}
.page-text{
  cursor: pointer;
  font-weight: bold;
  font-size: 200px;
  color: white;
  width: fit-content;
}
.page-text-changed{
  transition: padding 0.5s;
  padding: 0 5vw;
}
.page-text-back{
  transition: padding 0.5s;
  padding: 0 0vw;
}
.go-back-button{
  background-color: transparent;
  border: 0;
  cursor: pointer;
  color: white;
  font-size: 1em;
  position: fixed;
  right: 2vw;
  top: 2vh;
  padding: 0.3em;
  border-radius: 0.3em;
}
.about-front{
  width: 96vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font:inherit;
}
@media only screen and (max-width:500px) {
  .about-front-text{
    font-size: 2em;
  }
}
@media only screen and (min-width:500px) {
  .about-front-text{
    font-size: 3.333em;
  }
}

.about-front-text{
  /* position: relative; */
  font-family: Corben, serif;
  text-align: center;
  width: 86%;
  /* height: 30%; */
  /* display: flex; */
}
.about-story{
  width: 97vw;
  /* height: 100vh; */
  display: flex;
  align-items: center;
  /* flex-direction: column; */
  font:inherit;
  gap: 3em;
}

@media screen and (max-width: 800px) {
  .about-story{
    flex-direction: column;
  }
}
h1{
  font-family: Garamond, serif;
}
h3{
  font-family: Garamond, serif;
}
h4{
  font-family: Garamond, serif;
}
.about-story-text{
  display: block;
  height: 100%;
  text-align: center;
  width: 90%;
  font-size: 1.2em;
}
#about-header{
  font-size: 2.5em;
}
#resume-link{
  margin-top: 1em;
  font-size: 1.5em;
  background-color: black;
  border-radius: 1em;
  width: fit-content;
  padding: 0.3em;
  cursor: pointer;
  color: red;
  text-decoration: none;
}
#resume-link:hover{
  background-color: #888;
}
#contact{
  display: flex;
  padding: 4vh 2vw;
  align-items: center;
  justify-content: center;
}
.contact-socials{
  width: 48vw;
}
.projects-icons{
  width: 30vw;
  aspect-ratio: 0.9;
  cursor: pointer;
}

.socials-container{
  /* position: relative;
  left: 0%;
  top: 30%; */
  font-size: 2.5em;
  font-weight: bold;
}
.perspective-line-vertical{
  transform: skew(0deg,-30deg) scaleY(1.333);
}
.perspective-line-horizontal{
  transform: skew(60deg,-30deg) scaleY(.667);
}
.perspective-line{
  position: relative;
  overflow: hidden;
  display: block;
  width: fit-content;
  height: 9vh;
  z-index: 10;
}
.my-social{
  position: relative;
  margin: 0;
}
.send-email{
  margin: 5% auto;
  display: flex;
  flex-direction: column;
}
#send-email-result{
  color: greenyellow;
  font-size: 1.4em;
  height: 1.4em;
  text-align: center;
}
.perspective-move{
  transform: translateY(-1.5em);
  transition: transform 1s ease-in-out;
}
.social-ref{
  font-size: 0.8em;
  text-decoration: none;
  cursor: pointer;
  color: purple;
}
.email-input{
  background: transparent;
  border: none;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  font-size: 1.4em;
}
h3{
  font-size: 1.4em;
  text-align: center;
}
@media only screen and (max-width:830px) {
  .perspective-line{
    height: 20vw;
  }
  .contact-socials{
    display: none;
  }

}
@media only screen and (max-width: 1000px) {
  #contact{
    flex-direction: column;
  }
  .contact-socials{
    margin-top: 30%;
    position: relative;
    left: 100px;
  }
  .socials-container{
    height: 500px;
    top:15%;
  }
  .email-input{
    width: 250px;
  }
  .link-image{
    width: 40px;
    height: 40px;
  }
  .perspective-line{
    height: 9vh;
  }
}
@media only screen and (min-width: 1000px) {
  .perspective-line{
    height: 90px;
  }
}
@media only screen and (min-width: 1400px) {
  .perspective-line{
    height: 1.2em
  }
}
@media only screen and (min-width: 2000px) {
  .perspective-line{
    height: 1.5em
  }
  .my-social{
    font-size: 1.2em;
  }
  .link-image{
    width: 0.7em;
    height: 0.7em;
  }
}
@media only screen and (min-width:100px){
  .page-text{
    font-size: 3em;
  }
}
