  .page-text{
    font-size: 80px;
  }
} */

@media only screen and (max-width:700px){
  #front-page-image{
    display: none;
  }
}

@media only screen and (min-width:800px){
  #front-page-image{
    left: 20%;
  }
}

@media only screen and (min-width:2000px){
  .front-page-text{
    font-size: 1.5em;
  }
}



h1 {
  font-family: cursive;
  font-weight: bolder;
  text-align: center;
  color: white;
  letter-spacing: 0.1rem;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.66);
}
h2 {
  font-family: cursive;
  font-weight: bolder;
  text-align: center;
  color: white;
  letter-spacing: 0.1rem;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.66);
}
@media screen and (min-width: 1000px) {
  h2{
    font-size: 2em;
  }
}


.project{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15vh;
}

.project-left{
  align-self: flex-start;
}
.project-right{
  align-self: flex-end;
}


.mySkill{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 3em;
  gap: 0.1em;
}

.mySkill-icon{
  width: 100%;
  aspect-ratio: 1;
}
.mySkill-name{
  font-size: 1em;
}

.about-story-skills{
  width: calc(100%-0.6em);
  text-align: center;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.3em;
  gap: 2em;
  row-gap: 1em;
  flex-wrap: wrap;
  /* grid-template-columns: repeat(18%); */
}

.about-bottom{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3em;
  padding-bottom: 5em;
}


.submit-div{
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 1em;
  margin-top: 1em;
}

form{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: fit-content;
}

@media screen and (min-width: 2000px) {
  .socials-container{
    position: relative;
    left: 10vw;
  }
}

@media screen and (max-width: 1100px) {
  .send-email{
    margin: 0;
    width: 30%;
  }
  .email-input{
    max-width: 100%;
  }
  #contact{
    justify-content: space-between;
  }
}

@media screen and (max-width: 800px) {
  .send-email{
    margin: 0;
    width: 70%;
    align-items: center;
  }
  .email-input{
    max-width: fit-content;
  }

}

button{
  transition: 0.3s background-color;
}

button:hover{
  background-color: #888;
}